
(function () {
    "use strict";
    require("./contact-details.component.scss");
    require("./contact-details.service");
    var ctrl = function ($rootScope, $state, $timeout, $anchorScroll, contactDetailsSvc, analyticService, journeyService, UtilService) {
        var vm = this, txnId = null;
        var securityParams = {
            robocheck: vm.robocheck ? vm.robocheck : ''
        };

        vm.$onInit = function () {
            $rootScope.showSelectedCard = true;
            $rootScope.stepsHeader.currentStep = 0;
            contactDetailsSvc.clearAppButCardData();
            vm.isStaffJourney = journeyService.isStaffJourney();
            vm.selectedCard = journeyService.getSelectedCard();
            console.log("Selected card==", vm.selectedCard.key + "  and selected journey is ==", vm.isStaffJourney);
            var selectedCardKey = vm.selectedCard.key;
            // vm.incomeDocType = ['Standard', 'Other Bank Card'];
            vm.vcipType = ['Yes', 'No'];
            vm.ekyc = ['Offline XML', 'Physical copies', 'KYC Validated'];
            vm.ekycMSF = ['eKYC', 'Offline XML', 'Physical copies', 'KYC Validated'];
            vm.ekycDIY = ['Yes (I will keep my Aadhaar ready)', 'No (I will fill the details manually)'];
            vm.cardType = ['Primary Card', 'Supplementary Card'];
            vm.relationWithBank = ['New to Bank', 'Existing to Bank'];
            //added by ankit for hrp card 18112024 sprint2
            // if(vm.selectedCard.key === 'HPR' || vm.selectedCard.key === 'SVC'){
            if (vm.selectedCard.key === 'SVC') {
                vm.channelname = ['Branch', 'MSF'];
            }
            else {
                vm.channelname = ['MSF', 'TeleSales', 'TPSA', 'Branch', 'Digital'];
            }
            vm.dsaCodeType = [];
            vm.tajChamberMember = ['Yes', 'No'];
            if (vm.isStaffJourney) {
                if(selectedCardKey != 'SVC'){
                contactDetailsSvc.getDSACodeDetail().then(function (result) {
                    vm.dsaCodeType = result;
                });
                }
                else{
                    vm.dsaCodeType = ['TAJ']; 
                }

            }
            vm.regenerateOTP = true;
            vm.model = {};
            $anchorScroll();
            analyticService.trackPageLoad(analyticService.pageList.contactDetails);
        };

        vm.next = function (contactForm) {
            //alert(vm.model.incomeDocType +'=='+vm.model.creditCardLimit);
            vm.errMsg = "";
            vm.vcipFlag = 'N';
            if (vm.selectedCard.key === 'HPR' && vm.model.channelname === 'TPSA') {
                vm.errMsg = "TPSA stsff are not allowed to login premier application";
                vm.submitted = true;
                window.scrollTo(0, 0);
                return;
            }
            if (contactForm.$valid
                //  && !contactForm.dateOfBirth.$invalid
            ) {
                // var securityParams = {
                //     robocheck: vm.robocheck ? vm.robocheck : ''
                // };

                if (vm.model.vcipType === 'Yes') {
                    vm.vcipFlag = 'Y';
                    vm.model.vcipType = 'Y';
                } else if (vm.model.vcipType === 'No') {
                    vm.vcipFlag = 'N';
                    vm.model.vcipType = 'N';
                }

                if (vm.model.ekyc === 'eKYC') {
                    vm.model.ekyc = "Yes";
                } else if (vm.model.ekyc === 'Offline XML') {
                    vm.model.ekyc = "Offline XML";
                } else if (vm.model.ekyc === 'Physical copies') {
                    vm.model.ekyc = "Physical copies";
                }
                if (vm.selectedCard.key === 'HPR' && vm.model.cardType === 'Primary Card') {
                    // console.log("dsaCode ===", vm.model.dsaCode, vm.selectedCard.key, vm.model.cardType);

                    vm.model.dsaCode = 'PMR';
                } else if (vm.selectedCard.key === 'HPR' && vm.model.cardType === 'Supplementary Card') {
                    vm.model.dsaCode = 'PMA';
                }
                // console.log("eKYC type ===", vm.model.ekyc);
                var reqData = {
                    email: vm.model.email,
                    fullName: vm.model.fullName,
                    mobile: vm.model.mobile,
                    psid: vm.model.psid,
                    dsaCode: vm.model.dsaCode,
                    clusterCode: vm.model.clusterCode,
                    incomeDocType:"Standard",
                    creditCardLimit: vm.model.creditCardLimit,
                    salesChannelName: vm.model.channelname,
                    vcipFlag: vm.vcipFlag,
                    ekyc: vm.model.ekyc,
                    cardType: vm.model.cardType,
                    relationWithBank: vm.model.relationWithBank,
                    customerId: vm.model.customerId,
                    tajChamberMember :  vm.model.tajChamberMember
                }

                console.log("Staff journey contact details page data object === ", JSON.stringify(reqData));
                //alert(JSON.stringify(vm.model));
                contactDetailsSvc.submitContactDetails(reqData, securityParams).then(function (result) {
                    vm.psidContinue = false;
                    if (result.success) {
                        vm.staffOtpSent = true;
                        vm.psidContinue = true;
                        txnId = result.txnId;
                        var leadData = angular.merge({}, vm.model, { leadId: result.leadId, adobeId: result.internalRefNum });
                        contactDetailsSvc.setContactDetails(leadData);
                        //$state.go('app.journey.s3-personalDetails');
                        //$state.go('app.journey.s3-personalDetails',{'cardKey':vm.selectedCard.key});
                        /*
                            @AD - 07/06/2022 - above code (Currently line 96) has been commented due to otp verification for
                            staff assisted journey
                        */
                    } else {
                        if (result.errorCode === 10000) {
                            // Invalid PSID: PSID doesn't exist
                            vm.errMsg = "Invalid PSID";
                            analyticService.buttonList.error.event_content = "Invalid PSID error";
                            analyticService.trackUserAction(analyticService.buttonList.error);
                            window.scrollTo(0, 0);

                        } else if (result.errorCode === 10001) {
                            // Inactive PSID
                            vm.errMsg = "Inactive PSID";
                            analyticService.buttonList.error.event_content = "Inactive PSID error";
                            analyticService.trackUserAction(analyticService.buttonList.error);
                            window.scrollTo(0, 0);

                        } else {
                            vm.errMsg = result.message;
                            window.scrollTo(0, 0);

                        }
                    }
                });
                analyticService.buttonList.buttonClick.event_content = "Contact Details: Continue";
                analyticService.trackUserAction(analyticService.buttonList.buttonClick);
            } else {
                vm.submitted = true;
                UtilService.focusOnInvalid("contactForm");
            }
        };

        vm.verifyOtp = function (contactForm) {
            if (contactForm.otpstaff.$valid) {
                // var securityParams = {
                //     robocheck: vm.robocheck ? vm.robocheck : ''
                // };
                var staffotp = UtilService.encodeData2(vm.model.otpstaff);
                var reqData = {
                    otp: staffotp,
                    txnId: txnId,
                    psid: vm.model.psid
                }
                // console.log("Staff journey contact details page data object === ", JSON.stringify(reqData));

                contactDetailsSvc.verifyOtp(reqData, securityParams).then(function (result) {
                    vm.staffOtpSent = false;
                    if (result.success == 'true') {
                        // $state.go('app.journey.s3-personalDetails',{'cardKey':vm.selectedCard.key});
                        if (vm.model.relationWithBank == "Existing to Bank") {
                            console.log("ETB going for validateion")
                            var validateStaffNameReqData = { // added this obj for validate staff name 
                                email: vm.model.email,
                                fullName: vm.model.fullName,
                                mobile: vm.model.mobile,
                                psid: vm.model.psid,
                                dsaCode: vm.model.dsaCode,
                                clusterCode: vm.model.clusterCode,
                                    incomeDocType: "Standard",
                                creditCardLimit: vm.model.creditCardLimit,
                                salesChannelName: vm.model.channelname,
                                vcipFlag: vm.vcipFlag,
                                ekyc: vm.model.ekyc,
                                cardType: vm.model.cardType,
                                relationWithBank: vm.model.relationWithBank,
                                customerId: vm.model.customerId
                            }

                            contactDetailsSvc.validateStaffName(validateStaffNameReqData, securityParams).then(function (result) {
                                vm.staffOtpSent = false;
                                console.log("validateStaffNameReqData---->", validateStaffNameReqData);
                                console.log("validateStaffNameResult-->", result);
                                if (result.success == true) {
                                    // console.log("case sucess",result);
                                    contactDetailsSvc.setValidateStaffDetails(result);
                                    $state.go('app.journey.s3-personalDetails', { 'cardKey': vm.selectedCard.key });
                                }
                                else {
                                    console.log("errorCode-->", result.errorCode);
                                    // console.log("this log for case falied",result);
                                    if (result.errorCode == 100009 || result.errorCode == 1000010) {
                                        vm.errMsg = result.message;
                                    }
                                    else {
                                        vm.errMsg = "Sorry! there seems to be some technical error.";
                                    }
                                    // result.errorCode == 100009 ? vm.errMsg = result.message : vm.errMsg = "Sorry! there seems to be some technical error.";
                                    $timeout(function () {
                                        vm.errMsg = false;
                                    }, 20000);
                                    window.scrollTo(0, 0);
                                }
                            })
                        }
                        else {
                            // console.log("NTB ")

                            $state.go('app.journey.s3-personalDetails', { 'cardKey': vm.selectedCard.key });

                        }
                    } else {
                        if (result.errorCode === 1015) {
                            vm.errMsg = 'Your ID has been deactivated due to multiple invalid attempts.';
                        } else if (result.errorCode === 1003) {
                            vm.errMsg = 'Invalid OTP';
                         }else if (result.errorCode === 1005) {
                            vm.errMsg = 'The OTP has expired, please retry';
                         }
                          else {
                            vm.errMsg = "Sorry! there seems to be some technical error.";
                         }
                        window.scrollTo(0, 0);
                    }
                })
            }
        }

        vm.generateOtp = function (contactForm) {
            vm.errMsg = "";
            vm.showOtpSent = false;
            if (contactForm
                // .fullName.$valid && contactForm.mobileNo.$valid && contactForm.email.$valid &&
                // contactForm.ekyc.$valid && contactForm.salariedChk.$valid
            ) {
                // var securityParams = {
                //     robocheck: vm.robocheck ? vm.robocheck : ''
                // };
                var reqData = {
                    email: vm.model.email,
                    fullName: vm.model.fullName,
                    mobile: vm.model.mobile,
                    txnId: txnId,
                    cardType: vm.selectedCard.key,
                    salariedConsent: vm.model.salariedConsent ? 'Y' : 'N'
                }
                console.log("req data sending from frontend:" + JSON.stringify(reqData));
                contactDetailsSvc.generateOTP(reqData, securityParams).then(function (result) {
                    if (result.success) {
                        txnId = result.txnId;
                        vm.otpSent = true;
                        vm.showOtpSent = true;
                        vm.submitted = false;
                        vm.regenerateOTP = false;
                        vm.counter = 90;
                        countDown();
                        // $timeout(function () {
                        //     vm.regenerateOTP = true;
                        // }, 1.5 * 60 * 1000);
                    } else if (result.errorCode === 1023) {
                        // OTP Re-generate count exceeded the limit                        
                        contactDetailsSvc.setContactDetails(vm.model);
                        $state.go("app.journey.lead-generation", {
                            leadData: {
                                statusCode: result.errorCode,
                                fullName: vm.model.fullName,
                                mobileNumber: vm.model.mobile,
                                email: vm.model.email,
                                residenceCity: null,
                                leadReason: result.message
                            }
                            , cardKey: vm.selectedCard.key
                        });
                        analyticService.buttonList.error.event_content = "OTP Re-generate count exceeded the limit error";
                        analyticService.trackUserAction(analyticService.buttonList.error);
                    } else if (result.errorCode === 10006) {
                        //arun else if condition
                        //vm.errMsg = "Sorry! there seems to be some technical error. Please try again";
                        $state.go('app.journey.s1-cards');
                    }
                    else {
                        $anchorScroll();
                        vm.showOtpSent = false;
                        vm.errMsg = "Sorry! there seems to be some technical error. Please try again";
                        analyticService.buttonList.error.event_content = "No Records found";
                        analyticService.trackUserAction(analyticService.buttonList.error);
                    }
                });
                analyticService.buttonList.buttonClick.event_content = "Contact Details: Generate OTP";
                analyticService.trackUserAction(analyticService.buttonList.buttonClick);
            } else {
                // vm.otpSent = false;
                // vm.showOtpSent = false;
                vm.submitted = true;
                UtilService.focusOnInvalid('contactForm');
            }
        };

        var countDown = function () {
            $timeout(function () {
                vm.counter--;
                if (vm.counter <= 0) {
                    vm.regenerateOTP = true;
                } else {
                    countDown();
                }
            }, 1000);
        };

        vm.validateOtp = function (isValid) {
            vm.errMsg = "";
            vm.showOtpSent = false;
            if (isValid) {

                if (vm.model.ekyc === 'Yes (I will keep my Aadhaar ready)') {
                    vm.model.ekyc = "Yes";
                } else {
                    vm.model.ekyc = "No";
                }

                // console.log("eKYC type ===", vm.model.ekyc);
                vm.submitted = false;
                // var securityParams = {
                //     robocheck: vm.robocheck ? vm.robocheck : ''
                // };
                var reqData = {
                    txnId: txnId,
                    otp: vm.model.otp,
                    email: vm.model.email,
                    fullName: vm.model.fullName,
                    mobile: vm.model.mobile,
                    ekyc: vm.model.ekyc,
                    salariedConsent: vm.model.salariedConsent ? 'Y' : 'N',
                    cardType: vm.selectedCard.key,
                    tajChamberMember :  vm.model.tajChamberMember

                    // dateOfBirth: document.getElementById("dateOfBirth").value,
                }
                console.log("Data from Contact Details page ======= ", JSON.stringify(reqData));
                contactDetailsSvc.validateOTP(reqData, securityParams).then(function (result) {
                    if (result.success == 'true') {
                        //alert("result.txnId" + result.txnId);
                        var leadData = angular.merge({}, vm.model, { leadId: result.leadId });
                        contactDetailsSvc.setContactDetails(leadData);


                        //$state.go('app.journey.s3-personalDetails');
                        $state.go('app.journey.s3-personalDetails', { 'cardKey': vm.selectedCard.key });
                    } else {
                        $anchorScroll();
                        vm.submitted = true;
                        // vm.showOtpSent = false;
                        // vm.showErr = true;
                        var evtContent = "";
                        if (result.errorCode === 1005) {
                            // OTP expired
                            vm.errMsg = "Expired OTP, please regenerate OTP and retry";
                            evtContent = "OTP expired error";
                        } else if (result.errorCode === 1003) {
                            // Invalid OTP
                            vm.errMsg = "Invalid OTP";
                            evtContent = "Invalid OTP error";
                        } else if (result.errorCode === 1015) {
                            // OTP retry count exceeded, redirect to lead form
                            var leadData = angular.merge({}, vm.model, { leadId: result.leadId });
                            contactDetailsSvc.setContactDetails(leadData);
                            $state.go("app.journey.lead-generation", {
                                leadData: {
                                    statusCode: result.errorCode,
                                    fullName: vm.model.fullName,
                                    mobileNumber: vm.model.mobile,
                                    email: vm.model.email,
                                    residenceCity: null,
                                    leadReason: result.message
                                }
                                , cardKey: vm.selectedCard.key
                            });
                            evtContent = "OTP retry count exceeded";
                        } else if (result.errorCode === 10006) {
                            //arun else if condition
                            //vm.errMsg = "Sorry! there seems to be some technical error. Please try again";
                            $state.go('app.journey.s1-cards');
                        }
                        else {
                            vm.errMsg = "Sorry! there seems to be some technical error.";
                            evtContent = "Some technical error";
                        }
                        analyticService.buttonList.error.event_content = evtContent;
                        analyticService.trackUserAction(analyticService.buttonList.error);
                    }
                });
                analyticService.buttonList.buttonClick.event_content = "Contact Details: Continue";
                analyticService.trackUserAction(analyticService.buttonList.buttonClick);
            } else {
                vm.submitted = true;
                UtilService.focusOnInvalid('contactForm');
            }
        };
        vm.getIfTpsaAllowed = function () {
            console.log("Selected card==", vm.selectedCard.key + "  and selected journey is ==", vm.isStaffJourney + "and channel is==" + vm.model.channelname);
            if (vm.selectedCard.key === 'HPR' && vm.model.channelname === 'TPSA') {
                vm.errMsg = "TPSA stsff are not allowed to login premier application";
                window.scrollTo(0, 0);
            } else {
                vm.errMsg = "";
                console.log("Go ahead:");
            }
        }
    };
    var comp = angular.module("journey.module").component("contactDetails", {
        template: require("./contact-details.component.html"),
        controller: ["$rootScope", "$state", "$timeout", "$anchorScroll", "contactDetailsSvc", "analyticService", "journeyService", "UtilService", ctrl]
    });
    module.exports = comp.name;
})();


